import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Home from './Home'
import Viget from './coverLetter/Viget'

const Main = () => (
    <main>
        <Switch>
            <Route exact path='/' component={Home}/>
            <Route path='/viget' component={Viget}/>
        </Switch>
    </main>
)

export default Main



