import React, {Component} from 'react'
import { Link } from 'react-router-dom'
// import { TweenMax, TimelineMax, Power2 } from 'gsap'
// import GSAP from 'react-gsap-enhancer'
import SVGInline from 'react-svg-inline'
import './Viget.css'
import vLogo from './viget.svg'
import getWeird from './gifs/getWeird.gif'
import happyDance from './gifs/happyDance.gif'
import learning from './gifs/learning.gif'
import workTogether from './gifs/letsWorkTogether.gif'
import amazingWork from './gifs/seeAmazingWork.gif'


class Viget extends Component {

    render() {
        return (
            <div className="viget">
                <header className="intro">
                    <SVGInline svg={ vLogo } className="viget-logo"/>
                    <h1>Hi, it's nice to meet you!</h1>
                    <p><span role="img" aria-label="hello!"></span></p>
                </header>

                <section className="about-me">
                    <article>
                        <div className="heading">
                            <h2>A little about myself</h2>
                            <figure><img src={ getWeird } alt=""/></figure>
                        </div>
                        <div className="content">
                            <p>test content goes here</p>
                        </div>
                    </article>
                </section>


                <section className="why-viget">
                    <article>
                        <div className="heading">
                            <h2>why viget? let me tell you</h2>
                            <figure><img src={ amazingWork } alt=""/></figure>
                        </div>
                        <div className="content">
                            <p>test content goes here</p>
                        </div>
                    </article>
                </section>

                <section className="excitement">
                    <article>
                        <div className="heading">
                            <h2>potential of learning</h2>
                            <figure><img src={ happyDance } alt=""/></figure>
                        </div>
                        <div className="content">
                            <p>test content goes here</p>
                        </div>
                    </article>
                </section>

                <section className="resume">
                    <article>
                        <div className="heading">
                            <h2>Resume</h2>
                            <figure><img src={ learning } alt=""/></figure>
                        </div>
                        <div className="content">
                            <Link to='/resume.pdf'>View My Resume</Link>
                        </div>
                    </article>
                </section>

                <section className="references">
                <article>
                    <div className="heading">
                        <h2>References</h2>
                        <figure><img src={ workTogether } alt=""/></figure>
                    </div>
                    <div className="content">
                        <p>test content goes here</p>
                    </div>
                </article>
                </section>
            </div>
        )
    }
}

export default (Viget)



